"use client"

import {
  Settings,
  User,
  Shield,
  Bell,
  Smartphone,
  Key,
  Eye,
  EyeOff,
  CheckCircle2,
  Camera,
  Mail,
  Phone,
  MapPin,
  Calendar,
  FileText,
  Lock,
  AlertTriangle,
  Loader2,
  QrCode,
  Copy,
} from "lucide-react"
import { useState } from "react"

const tabs = [
  { id: "perfil", label: "Perfil", icon: User },
  { id: "seguranca", label: "Seguranca", icon: Shield },
  { id: "pin", label: "PIN", icon: Lock },
  { id: "2fa", label: "2FA", icon: Key },
  { id: "sessoes", label: "Sessoes", icon: Smartphone },
  { id: "notificacoes", label: "Notificacoes", icon: Bell },
]

const sessions = [
  { id: "1", device: "Chrome - Windows", location: "Sao Paulo, BR", lastActive: "Agora", current: true },
  { id: "2", device: "Safari - iPhone 15", location: "Sao Paulo, BR", lastActive: "2h atras", current: false },
  { id: "3", device: "Firefox - MacOS", location: "Rio de Janeiro, BR", lastActive: "1 dia atras", current: false },
]

export default function ConfiguracoesPage() {
  const [activeTab, setActiveTab] = useState("perfil")
  const [showPassword, setShowPassword] = useState(false)
  const [showCurrentPin, setShowCurrentPin] = useState(false)
  const [showNewPin, setShowNewPin] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [twoFactorEnabled, setTwoFactorEnabled] = useState(true)
  const [show2FASetup, setShow2FASetup] = useState(false)
  const [verificationCode, setVerificationCode] = useState("")
  const [notifications, setNotifications] = useState({
    email: true,
    push: true,
    discord: false,
    marketing: false,
    transacoes: true,
    seguranca: true,
  })
  const [discordWebhook, setDiscordWebhook] = useState("")
  const [savedWebhook, setSavedWebhook] = useState("")
  const [webhookTesting, setWebhookTesting] = useState(false)
  const [webhookSaving, setWebhookSaving] = useState(false)
  const [webhookStatus, setWebhookStatus] = useState<"idle" | "success" | "error">("idle")
  const [webhookSaved, setWebhookSaved] = useState(false)
  const [testCooldown, setTestCooldown] = useState(0)

  const [pinForm, setPinForm] = useState({
    currentPin: "",
    newPin: "",
    confirmPin: "",
  })

  const handlePinChange = (field: string, value: string) => {
    const numericValue = value.replace(/\D/g, "").slice(0, 6)
    setPinForm(prev => ({ ...prev, [field]: numericValue }))
  }

  const handlePinSubmit = async () => {
    if (pinForm.newPin !== pinForm.confirmPin) {
      alert("Os PINs nao coincidem")
      return
    }
    if (pinForm.newPin.length !== 6) {
      alert("O PIN deve ter 6 digitos")
      return
    }
    setIsLoading(true)
    await new Promise(resolve => setTimeout(resolve, 1500))
    setIsLoading(false)
    setPinForm({ currentPin: "", newPin: "", confirmPin: "" })
  }

  const handleTestWebhook = async () => {
    if (!discordWebhook || testCooldown > 0) return
    
    setWebhookTesting(true)
    setWebhookStatus("idle")
    
    try {
      const response = await fetch("/api/notifications/discord", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          webhookUrl: discordWebhook,
          type: "teste",
        }),
      })
      
      if (response.ok) {
        setWebhookStatus("success")
        // Start 30 second cooldown
        setTestCooldown(30)
        const interval = setInterval(() => {
          setTestCooldown(prev => {
            if (prev <= 1) {
              clearInterval(interval)
              return 0
            }
            return prev - 1
          })
        }, 1000)
      } else {
        setWebhookStatus("error")
      }
    } catch {
      setWebhookStatus("error")
    } finally {
      setWebhookTesting(false)
    }
  }

  const handleSaveWebhook = async () => {
    if (!discordWebhook) return
    
    setWebhookSaving(true)
    setWebhookSaved(false)
    
    // Simulate saving to backend
    await new Promise(resolve => setTimeout(resolve, 1000))
    
    setSavedWebhook(discordWebhook)
    setWebhookSaving(false)
    setWebhookSaved(true)
    
    // Reset saved indicator after 3 seconds
    setTimeout(() => setWebhookSaved(false), 3000)
  }

  const handleDiscordToggle = () => {
    if (notifications.discord) {
      setNotifications({ ...notifications, discord: false })
      setDiscordWebhook("")
      setSavedWebhook("")
      setWebhookStatus("idle")
      setWebhookSaved(false)
      setTestCooldown(0)
    } else {
      setNotifications({ ...notifications, discord: true })
    }
  }

  return (
    <div className="space-y-4 sm:space-y-6">
      <div>
        <h1 className="text-xl sm:text-2xl font-bold text-white">Configuracoes</h1>
        <p className="text-zinc-400 text-xs sm:text-sm mt-1">Gerencie suas preferencias e seguranca</p>
      </div>

      <div className="flex flex-col lg:flex-row gap-6">
        <div className="w-full lg:w-64 flex-shrink-0">
          <nav className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-2 flex lg:block gap-1 overflow-x-auto">
            {tabs.map((tab) => (
              <button
                key={tab.id}
                type="button"
                onClick={() => setActiveTab(tab.id)}
                className={`flex-shrink-0 lg:w-full flex items-center gap-2 lg:gap-3 px-3 lg:px-4 py-2.5 lg:py-3 rounded-lg transition-colors whitespace-nowrap ${
                  activeTab === tab.id
                    ? "bg-emerald-500/10 text-emerald-400"
                    : "text-zinc-400 hover:text-white hover:bg-zinc-800/50"
                }`}
              >
                <tab.icon className="w-4 h-4 lg:w-5 lg:h-5" />
                <span className="text-xs lg:text-sm font-medium">{tab.label}</span>
              </button>
            ))}
          </nav>
        </div>

        <div className="flex-1">
          {activeTab === "perfil" && (
            <div className="space-y-4 sm:space-y-6">
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <h2 className="text-sm sm:text-lg font-semibold text-white mb-4 sm:mb-6">Foto de perfil</h2>
                <div className="flex items-center gap-4 sm:gap-6">
                  <div className="relative">
                    <div className="w-16 h-16 sm:w-24 sm:h-24 rounded-full bg-emerald-500/20 flex items-center justify-center">
                      <span className="text-emerald-400 font-bold text-lg sm:text-2xl">JD</span>
                    </div>
                    <button
                      type="button"
                      className="absolute bottom-0 right-0 w-6 h-6 sm:w-8 sm:h-8 bg-emerald-500 hover:bg-emerald-400 rounded-full flex items-center justify-center transition-colors"
                    >
                      <Camera className="w-3 h-3 sm:w-4 sm:h-4 text-zinc-900" />
                    </button>
                  </div>
                  <div>
                    <p className="text-white font-medium text-sm sm:text-base">Joao da Silva</p>
                    <p className="text-zinc-500 text-xs sm:text-sm">Membro desde Janeiro 2024</p>
                  </div>
                </div>
              </div>

              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <h2 className="text-sm sm:text-lg font-semibold text-white mb-4 sm:mb-6">Informacoes pessoais</h2>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
                  <div>
                    <label className="flex items-center gap-2 text-sm font-medium text-zinc-300 mb-2">
                      <User className="w-4 h-4" />
                      Nome completo
                    </label>
                    <input
                      type="text"
                      defaultValue="Joao da Silva"
                      className="w-full px-4 py-3 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white focus:outline-none focus:border-emerald-500/50 transition-colors"
                    />
                  </div>
                  <div>
                    <label className="flex items-center gap-2 text-sm font-medium text-zinc-300 mb-2">
                      <FileText className="w-4 h-4" />
                      CPF
                    </label>
                    <input
                      type="text"
                      defaultValue="123.456.789-00"
                      disabled
                      className="w-full px-4 py-3 bg-zinc-800/30 border border-zinc-700/50 rounded-xl text-zinc-500 cursor-not-allowed"
                    />
                  </div>
                  <div>
                    <label className="flex items-center gap-2 text-sm font-medium text-zinc-300 mb-2">
                      <Mail className="w-4 h-4" />
                      E-mail
                    </label>
                    <input
                      type="email"
                      defaultValue="joao@email.com"
                      disabled
                      className="w-full px-4 py-3 bg-zinc-800/30 border border-zinc-700/50 rounded-xl text-zinc-500 cursor-not-allowed"
                    />
                  </div>
                  <div>
                    <label className="flex items-center gap-2 text-sm font-medium text-zinc-300 mb-2">
                      <Phone className="w-4 h-4" />
                      Telefone
                    </label>
                    <input
                      type="tel"
                      defaultValue="(11) 99999-9999"
                      className="w-full px-4 py-3 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white focus:outline-none focus:border-emerald-500/50 transition-colors"
                    />
                  </div>
                  <div>
                    <label className="flex items-center gap-2 text-sm font-medium text-zinc-300 mb-2">
                      <Calendar className="w-4 h-4" />
                      Data de nascimento
                    </label>
                    <input
                      type="text"
                      defaultValue="15/01/1990"
                      disabled
                      className="w-full px-4 py-3 bg-zinc-800/30 border border-zinc-700/50 rounded-xl text-zinc-500 cursor-not-allowed"
                    />
                  </div>
                  <div>
                    <label className="flex items-center gap-2 text-sm font-medium text-zinc-300 mb-2">
                      <MapPin className="w-4 h-4" />
                      CEP
                    </label>
                    <input
                      type="text"
                      defaultValue="01310-100"
                      className="w-full px-4 py-3 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white focus:outline-none focus:border-emerald-500/50 transition-colors"
                    />
                  </div>
                </div>
                <div className="mt-6 flex justify-end">
                  <button
                    type="button"
                    className="px-6 py-2.5 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 font-medium rounded-xl transition-colors"
                  >
                    Salvar alteracoes
                  </button>
                </div>
              </div>
            </div>
          )}

          {activeTab === "seguranca" && (
            <div className="space-y-6">
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <h2 className="text-sm sm:text-lg font-semibold text-white mb-4 sm:mb-6">Alterar senha</h2>
                <div className="space-y-5 max-w-md">
                  <div>
                    <label className="block text-sm font-medium text-zinc-300 mb-2">Senha atual</label>
                    <div className="relative">
                      <input
                        type={showPassword ? "text" : "password"}
                        placeholder="Digite sua senha atual"
                        className="w-full px-4 py-3 pr-12 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white placeholder:text-zinc-500 focus:outline-none focus:border-emerald-500/50 transition-colors"
                      />
                      <button
                        type="button"
                        onClick={() => setShowPassword(!showPassword)}
                        className="absolute right-4 top-1/2 -translate-y-1/2 text-zinc-500 hover:text-white"
                      >
                        {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                      </button>
                    </div>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-zinc-300 mb-2">Nova senha</label>
                    <input
                      type="password"
                      placeholder="Minimo 12 caracteres"
                      className="w-full px-4 py-3 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white placeholder:text-zinc-500 focus:outline-none focus:border-emerald-500/50 transition-colors"
                    />
                    <p className="text-xs text-zinc-500 mt-2">
                      Use letras maiusculas, minusculas, numeros e simbolos
                    </p>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-zinc-300 mb-2">Confirmar nova senha</label>
                    <input
                      type="password"
                      placeholder="Confirme sua nova senha"
                      className="w-full px-4 py-3 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white placeholder:text-zinc-500 focus:outline-none focus:border-emerald-500/50 transition-colors"
                    />
                  </div>
                  <div className="bg-amber-500/10 border border-amber-500/30 rounded-lg p-3">
                    <div className="flex items-start gap-2">
                      <AlertTriangle className="w-4 h-4 text-amber-400 flex-shrink-0 mt-0.5" />
                      <p className="text-amber-400 text-sm">
                        A alteracao de senha requer confirmacao via PIN de seguranca
                      </p>
                    </div>
                  </div>
                  <button
                    type="button"
                    className="px-6 py-2.5 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 font-medium rounded-xl transition-colors"
                  >
                    Atualizar senha
                  </button>
                </div>
              </div>
            </div>
          )}

          {activeTab === "pin" && (
            <div className="space-y-6">
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <div className="flex items-center gap-3 mb-6">
                  <div className="w-10 h-10 rounded-xl bg-emerald-500/10 flex items-center justify-center">
                    <Lock className="w-5 h-5 text-emerald-400" />
                  </div>
                  <div>
                    <h2 className="text-lg font-semibold text-white">PIN de Seguranca</h2>
                    <p className="text-zinc-500 text-sm">Usado para confirmar transferencias e acoes criticas</p>
                  </div>
                </div>

                <div className="bg-blue-500/10 border border-blue-500/30 rounded-lg p-4 mb-6">
                  <p className="text-blue-400 text-sm">
                    Seu PIN de 6 digitos e obrigatorio para transferencias, alteracao de senha e outras acoes sensiveis.
                    Apos 5 tentativas incorretas, sua conta sera bloqueada temporariamente.
                  </p>
                </div>

                <div className="space-y-5 max-w-md">
                  <div>
                    <label className="block text-sm font-medium text-zinc-300 mb-2">PIN atual</label>
                    <div className="relative">
                      <input
                        type={showCurrentPin ? "text" : "password"}
                        value={pinForm.currentPin}
                        onChange={(e) => handlePinChange("currentPin", e.target.value)}
                        placeholder="******"
                        maxLength={6}
                        className="w-full px-4 py-3 pr-12 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white text-center text-xl tracking-[0.5em] font-mono placeholder:text-zinc-600 placeholder:tracking-normal focus:outline-none focus:border-emerald-500/50 transition-colors"
                      />
                      <button
                        type="button"
                        onClick={() => setShowCurrentPin(!showCurrentPin)}
                        className="absolute right-4 top-1/2 -translate-y-1/2 text-zinc-500 hover:text-white"
                      >
                        {showCurrentPin ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                      </button>
                    </div>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-zinc-300 mb-2">Novo PIN</label>
                    <div className="relative">
                      <input
                        type={showNewPin ? "text" : "password"}
                        value={pinForm.newPin}
                        onChange={(e) => handlePinChange("newPin", e.target.value)}
                        placeholder="******"
                        maxLength={6}
                        className="w-full px-4 py-3 pr-12 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white text-center text-xl tracking-[0.5em] font-mono placeholder:text-zinc-600 placeholder:tracking-normal focus:outline-none focus:border-emerald-500/50 transition-colors"
                      />
                      <button
                        type="button"
                        onClick={() => setShowNewPin(!showNewPin)}
                        className="absolute right-4 top-1/2 -translate-y-1/2 text-zinc-500 hover:text-white"
                      >
                        {showNewPin ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                      </button>
                    </div>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-zinc-300 mb-2">Confirmar novo PIN</label>
                    <input
                      type="password"
                      value={pinForm.confirmPin}
                      onChange={(e) => handlePinChange("confirmPin", e.target.value)}
                      placeholder="******"
                      maxLength={6}
                      className="w-full px-4 py-3 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white text-center text-xl tracking-[0.5em] font-mono placeholder:text-zinc-600 placeholder:tracking-normal focus:outline-none focus:border-emerald-500/50 transition-colors"
                    />
                  </div>
                  <button
                    type="button"
                    onClick={handlePinSubmit}
                    disabled={isLoading || pinForm.newPin.length !== 6}
                    className="w-full flex items-center justify-center gap-2 px-6 py-3 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 font-medium rounded-xl transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    {isLoading ? (
                      <>
                        <Loader2 className="w-5 h-5 animate-spin" />
                        Atualizando...
                      </>
                    ) : (
                      "Atualizar PIN"
                    )}
                  </button>
                </div>
              </div>
            </div>
          )}

          {activeTab === "2fa" && (
            <div className="space-y-6">
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <div className="flex items-center justify-between mb-6">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-xl bg-emerald-500/10 flex items-center justify-center">
                      <Key className="w-5 h-5 text-emerald-400" />
                    </div>
                    <div>
                      <h2 className="text-lg font-semibold text-white">Autenticacao em duas etapas</h2>
                      <p className="text-zinc-500 text-sm">Google Authenticator (TOTP)</p>
                    </div>
                  </div>
                  {twoFactorEnabled && (
                    <span className="px-3 py-1 bg-emerald-500/10 text-emerald-400 text-sm font-medium rounded-full flex items-center gap-1">
                      <CheckCircle2 className="w-4 h-4" />
                      Ativado
                    </span>
                  )}
                </div>

                {twoFactorEnabled ? (
                  <div className="space-y-4">
                    <p className="text-zinc-400 text-sm">
                      A autenticacao em duas etapas esta ativa. Voce precisara do codigo do app autenticador
                      para fazer login e realizar acoes sensiveis.
                    </p>
                    <button
                      type="button"
                      onClick={() => setTwoFactorEnabled(false)}
                      className="px-4 py-2 text-red-400 hover:text-red-300 border border-red-500/30 hover:border-red-500/50 rounded-xl transition-colors"
                    >
                      Desativar 2FA
                    </button>
                  </div>
                ) : (
                  <div className="space-y-6">
                    {!show2FASetup ? (
                      <div>
                        <p className="text-zinc-400 text-sm mb-4">
                          Adicione uma camada extra de seguranca a sua conta usando o Google Authenticator.
                        </p>
                        <button
                          type="button"
                          onClick={() => setShow2FASetup(true)}
                          className="px-6 py-2.5 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 font-medium rounded-xl transition-colors"
                        >
                          Configurar 2FA
                        </button>
                      </div>
                    ) : (
                      <div className="space-y-6">
                        <div className="flex flex-col items-center p-6 bg-zinc-800/30 rounded-xl border border-zinc-700">
                          <p className="text-zinc-400 text-sm mb-4 text-center">
                            Escaneie o QR Code com o Google Authenticator
                          </p>
                          <div className="w-48 h-48 bg-white rounded-xl flex items-center justify-center mb-4">
                            <QrCode className="w-40 h-40 text-zinc-900" />
                          </div>
                          <div className="flex items-center gap-2 px-4 py-2 bg-zinc-900 rounded-lg">
                            <code className="text-zinc-400 text-sm font-mono">JBSWY3DPEHPK3PXP</code>
                            <button type="button" className="text-zinc-500 hover:text-white">
                              <Copy className="w-4 h-4" />
                            </button>
                          </div>
                          <p className="text-zinc-500 text-xs mt-2">Codigo manual para configuracao</p>
                        </div>

                        <div>
                          <label className="block text-sm font-medium text-zinc-300 mb-2">
                            Digite o codigo do app
                          </label>
                          <input
                            type="text"
                            value={verificationCode}
                            onChange={(e) => setVerificationCode(e.target.value.replace(/\D/g, "").slice(0, 6))}
                            placeholder="000000"
                            maxLength={6}
                            className="w-full max-w-xs px-4 py-3 bg-zinc-800/50 border border-zinc-700 rounded-xl text-white text-center text-xl tracking-[0.5em] font-mono placeholder:text-zinc-600 focus:outline-none focus:border-emerald-500/50 transition-colors"
                          />
                        </div>

                        <div className="flex gap-3">
                          <button
                            type="button"
                            onClick={() => setShow2FASetup(false)}
                            className="px-6 py-2.5 bg-zinc-800 hover:bg-zinc-700 text-white font-medium rounded-xl transition-colors"
                          >
                            Cancelar
                          </button>
                          <button
                            type="button"
                            onClick={() => {
                              setTwoFactorEnabled(true)
                              setShow2FASetup(false)
                              setVerificationCode("")
                            }}
                            disabled={verificationCode.length !== 6}
                            className="px-6 py-2.5 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 font-medium rounded-xl transition-colors disabled:opacity-50"
                          >
                            Verificar e ativar
                          </button>
                        </div>
                      </div>
                    )}
                  </div>
                )}
              </div>
            </div>
          )}

          {activeTab === "sessoes" && (
            <div className="space-y-6">
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <div className="flex items-center justify-between mb-6">
                  <h2 className="text-lg font-semibold text-white">Sessoes ativas</h2>
                  <button
                    type="button"
                    className="text-red-400 hover:text-red-300 text-sm font-medium"
                  >
                    Encerrar todas
                  </button>
                </div>
                <div className="space-y-3">
                  {sessions.map((session) => (
                    <div
                      key={session.id}
                      className={`flex items-center justify-between p-4 rounded-xl border ${
                        session.current
                          ? "bg-emerald-500/5 border-emerald-500/30"
                          : "bg-zinc-800/30 border-zinc-700"
                      }`}
                    >
                      <div className="flex items-center gap-4">
                        <div className="w-10 h-10 rounded-lg bg-zinc-800 flex items-center justify-center">
                          <Smartphone className="w-5 h-5 text-zinc-400" />
                        </div>
                        <div>
                          <p className="text-white font-medium">{session.device}</p>
                          <p className="text-zinc-500 text-sm">{session.location} â€¢ {session.lastActive}</p>
                        </div>
                      </div>
                      {session.current ? (
                        <span className="px-2 py-1 bg-emerald-500/10 text-emerald-400 text-xs font-medium rounded-full">
                          Sessao atual
                        </span>
                      ) : (
                        <button
                          type="button"
                          className="text-red-400 hover:text-red-300 text-sm font-medium"
                        >
                          Encerrar
                        </button>
                      )}
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}

          {activeTab === "notificacoes" && (
            <div className="space-y-6">
              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <h2 className="text-sm sm:text-lg font-semibold text-white mb-4 sm:mb-6">Canais de notificacao</h2>
                <div className="space-y-4">
                  {[
                    { key: "email", label: "E-mail", desc: "Receba notificacoes por e-mail" },
                    { key: "push", label: "Push", desc: "Notificacoes no navegador" },
                  ].map((item) => (
                    <div key={item.key} className="flex items-center justify-between p-4 bg-zinc-800/30 rounded-xl border border-zinc-700">
                      <div>
                        <p className="text-white font-medium">{item.label}</p>
                        <p className="text-zinc-500 text-sm">{item.desc}</p>
                      </div>
                      <button
                        type="button"
                        onClick={() => setNotifications({ ...notifications, [item.key]: !notifications[item.key as keyof typeof notifications] })}
                        className={`relative w-12 h-6 rounded-full transition-colors ${
                          notifications[item.key as keyof typeof notifications] ? "bg-emerald-500" : "bg-zinc-700"
                        }`}
                      >
                        <span
                          className={`absolute top-1 w-4 h-4 bg-white rounded-full transition-transform ${
                            notifications[item.key as keyof typeof notifications] ? "left-7" : "left-1"
                          }`}
                        />
                      </button>
                    </div>
                  ))}

                  {/* Discord Webhook */}
                  <div className="p-4 bg-zinc-800/30 rounded-xl border border-zinc-700">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-3">
                        <div className="w-8 h-8 rounded-lg bg-indigo-500/20 flex items-center justify-center">
                          <svg className="w-4 h-4 text-indigo-400" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M20.317 4.37a19.791 19.791 0 0 0-4.885-1.515a.074.074 0 0 0-.079.037c-.21.375-.444.864-.608 1.25a18.27 18.27 0 0 0-5.487 0a12.64 12.64 0 0 0-.617-1.25a.077.077 0 0 0-.079-.037A19.736 19.736 0 0 0 3.677 4.37a.07.07 0 0 0-.032.027C.533 9.046-.32 13.58.099 18.057a.082.082 0 0 0 .031.057a19.9 19.9 0 0 0 5.993 3.03a.078.078 0 0 0 .084-.028a14.09 14.09 0 0 0 1.226-1.994a.076.076 0 0 0-.041-.106a13.107 13.107 0 0 1-1.872-.892a.077.077 0 0 1-.008-.128a10.2 10.2 0 0 0 .372-.292a.074.074 0 0 1 .077-.01c3.928 1.793 8.18 1.793 12.062 0a.074.074 0 0 1 .078.01c.12.098.246.198.373.292a.077.077 0 0 1-.006.127a12.299 12.299 0 0 1-1.873.892a.077.077 0 0 0-.041.107c.36.698.772 1.362 1.225 1.993a.076.076 0 0 0 .084.028a19.839 19.839 0 0 0 6.002-3.03a.077.077 0 0 0 .032-.054c.5-5.177-.838-9.674-3.549-13.66a.061.061 0 0 0-.031-.03zM8.02 15.33c-1.183 0-2.157-1.085-2.157-2.419c0-1.333.956-2.419 2.157-2.419c1.21 0 2.176 1.096 2.157 2.42c0 1.333-.956 2.418-2.157 2.418zm7.975 0c-1.183 0-2.157-1.085-2.157-2.419c0-1.333.955-2.419 2.157-2.419c1.21 0 2.176 1.096 2.157 2.42c0 1.333-.946 2.418-2.157 2.418z"/>
                          </svg>
                        </div>
                        <div>
                          <p className="text-white font-medium">Discord Webhook</p>
                          <p className="text-zinc-500 text-sm">Receba alertas no seu servidor</p>
                        </div>
                      </div>
                      <button
                        type="button"
                        onClick={handleDiscordToggle}
                        className={`relative w-12 h-6 rounded-full transition-colors ${
                          notifications.discord ? "bg-indigo-500" : "bg-zinc-700"
                        }`}
                      >
                        <span
                          className={`absolute top-1 w-4 h-4 bg-white rounded-full transition-transform ${
                            notifications.discord ? "left-7" : "left-1"
                          }`}
                        />
                      </button>
                    </div>

                    {/* Webhook URL Input */}
                    {notifications.discord && (
                      <div className="mt-4 space-y-3">
                        <div>
                          <label className="block text-xs text-zinc-400 mb-1.5">URL do Webhook</label>
                          <input
                            type="text"
                            value={discordWebhook}
                            onChange={(e) => {
                              setDiscordWebhook(e.target.value)
                              setWebhookStatus("idle")
                            }}
                            placeholder="https://discord.com/api/webhooks/..."
                            className="w-full px-3 py-2.5 bg-zinc-800 border border-zinc-700 rounded-lg text-white text-sm placeholder:text-zinc-500 focus:outline-none focus:border-indigo-500/50 transition-colors"
                          />
                        </div>
                        <div className="flex flex-wrap items-center gap-2">
                          <button
                            type="button"
                            onClick={handleTestWebhook}
                            disabled={!discordWebhook || webhookTesting || testCooldown > 0}
                            className="flex items-center gap-2 px-3 py-2 bg-zinc-700 hover:bg-zinc-600 text-white text-sm font-medium rounded-lg transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                          >
                            {webhookTesting ? (
                              <>
                                <Loader2 className="w-4 h-4 animate-spin" />
                                Testando...
                              </>
                            ) : testCooldown > 0 ? (
                              `Aguarde ${testCooldown}s`
                            ) : (
                              "Testar"
                            )}
                          </button>
                          <button
                            type="button"
                            onClick={handleSaveWebhook}
                            disabled={!discordWebhook || webhookSaving || discordWebhook === savedWebhook}
                            className="flex items-center gap-2 px-3 py-2 bg-indigo-500 hover:bg-indigo-400 text-white text-sm font-medium rounded-lg transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                          >
                            {webhookSaving ? (
                              <>
                                <Loader2 className="w-4 h-4 animate-spin" />
                                Salvando...
                              </>
                            ) : (
                              "Salvar"
                            )}
                          </button>
                        </div>
                        <div className="flex flex-wrap items-center gap-2 min-h-[20px]">
                          {webhookStatus === "success" && (
                            <span className="flex items-center gap-1 text-emerald-400 text-xs">
                              <CheckCircle2 className="w-3.5 h-3.5" />
                              Teste OK
                            </span>
                          )}
                          {webhookStatus === "error" && (
                            <span className="flex items-center gap-1 text-red-400 text-xs">
                              <AlertTriangle className="w-3.5 h-3.5" />
                              Erro no teste
                            </span>
                          )}
                          {webhookSaved && (
                            <span className="flex items-center gap-1 text-emerald-400 text-xs">
                              <CheckCircle2 className="w-3.5 h-3.5" />
                              Webhook salvo!
                            </span>
                          )}
                        </div>
                        <p className="text-xs text-zinc-500">
                          Crie um webhook em: Discord {'>'} Config. do Canal {'>'} Integracoes
                        </p>
                      </div>
                    )}
                  </div>
                </div>
              </div>

              <div className="bg-zinc-900/50 border border-zinc-800 rounded-xl p-4 sm:p-6">
                <h2 className="text-sm sm:text-lg font-semibold text-white mb-4 sm:mb-6">Tipos de notificacao</h2>
                <div className="space-y-4">
                  {[
                    { key: "transacoes", label: "Transacoes", desc: "Depositos, transferencias e movimentacoes" },
                    { key: "seguranca", label: "Seguranca", desc: "Alertas de login e atividades suspeitas" },
                    { key: "marketing", label: "Marketing", desc: "Novidades, promocoes e dicas" },
                  ].map((item) => (
                    <div key={item.key} className="flex items-center justify-between p-4 bg-zinc-800/30 rounded-xl border border-zinc-700">
                      <div>
                        <p className="text-white font-medium">{item.label}</p>
                        <p className="text-zinc-500 text-sm">{item.desc}</p>
                      </div>
                      <button
                        type="button"
                        onClick={() => setNotifications({ ...notifications, [item.key]: !notifications[item.key as keyof typeof notifications] })}
                        className={`relative w-12 h-6 rounded-full transition-colors ${
                          notifications[item.key as keyof typeof notifications] ? "bg-emerald-500" : "bg-zinc-700"
                        }`}
                      >
                        <span
                          className={`absolute top-1 w-4 h-4 bg-white rounded-full transition-transform ${
                            notifications[item.key as keyof typeof notifications] ? "left-7" : "left-1"
                          }`}
                        />
                      </button>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
